import React, { FC } from 'react';

interface dementias {
  width?: boolean;
  height?: boolean;
}

export const Logo: FC<dementias> = (props) => {
  const styles: { height: string; width: string } = ((
    props
  ): { height: string; width: string } => {
    if (props.height) {
      return { height: '100%', width: 'auto' };
    } else if (props.width) {
      return { height: 'auto', width: '100%' };
    } else {
      return { height: 'auto', width: 'auto%' };
    }
  })(props);
  return (
    <img
      src="http://tksbuilding.co.uk/img/TKS_builder_company.png"
      alt="TKS building company logo."
      style={styles}
    />
  );
};
