import { PageSvc } from '../../services/PageSvc';

import React from 'react';
import { useLocation } from 'react-router-dom';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';

import { makeStyles } from '@material-ui/core/styles';
import { SideBar } from '../../components/SideBar/SideBar';

const useStyle = makeStyles({
  card: {
    display: 'flex',
    width: '100%',
    '& > *': {
      flex: 1,
    },
  },
});

export const Page = () => {
  const classes = useStyle();

  const location = useLocation();
  const pathname = location.pathname.slice(1);

  const category = PageSvc.getCategory(pathname);

  const pages = category?.subPages;
  const categoryName = category?.category;
  const subTitles = category?.subPages.map((subPage) => subPage.title);
  const sideBarLinks = PageSvc.getSideBarLinks(pathname);

  const content = pages?.map((page, index) => (
    <Grid item container key={page.title + index + index}>
      {subTitles && (
        <Card className={classes.card}>
          <CardActionArea>
            <CardMedia
              image="https://www.energy.gov/sites/prod/files/styles/borealis_photo_gallery_large_respondxl/public/door_5481543.jpg?itok=BpZlylNh"
              style={{ height: 250 }}
            />
          </CardActionArea>
          <CardContent>
            <Typography variant="h4" component="h4">
              {subTitles[index] === '' ? categoryName : subTitles[index]}
            </Typography>
          </CardContent>
        </Card>
      )}

      <div
        key={page.title + index}
        dangerouslySetInnerHTML={{ __html: page.content }}
      />
    </Grid>
  ));

  return (
    <Grid item container sm={8} justify="center" alignItems="flex-start">
      <Grid item sm={4} xs={false}>
        {sideBarLinks && <SideBar sideBarLinks={sideBarLinks} />}
      </Grid>

      <Grid item sm={8}>
        {content}
      </Grid>
    </Grid>
  );
};
