import React, { useState } from 'react';

import Slide from '@material-ui/core/Slide';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import NavigateBeforeIcon from '@material-ui/icons/NavigateBefore';
import NavigateNextIcon from '@material-ui/icons/NavigateNext';
import { makeStyles } from '@material-ui/core/styles';
import { SlideProps } from '@material-ui/core/Slide/Slide';

//mocking
import { data } from '../../consts/slides';

const useStyle = makeStyles({
  '@keyframes bgmove': {
    '0%': {
      backgroundPosition: '25% 50%',
    },
    '50%': {
      backgroundPosition: '75% 50%',
    },
    '100%': {
      backgroundPosition: '25% 50%',
    },
  },
  container: {
    width: '100vw',
    overflow: 'hidden',
  },
  animated: {
    animation: `$bgmove 20s`,
    // animationDelay: '1s',
    animationIterationCount: 'infinite',
  },
  slide: {
    height: 'calc(100vh - 64px)',
    width: '100vw',
    backgroundImage: (props: { image: string }) => `url(${props.image})`,
    backgroundRepeat: 'no-repeat',
    backgroundPosition: 'center center',
    backgroundSize: '110% auto',
    overflow: 'hidden',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
  },
  content: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
    width: '100%',
  },
  arrow: {
    cursor: 'pointer',
    margin: 25,
  },
});

export const Carousel = () => {
  const [currentSlide, setCurrentSlide] = useState<number>(0);
  const [nextSlide, setNextSlide] = useState<number>(currentSlide);
  const [direction, setDirection] = useState<SlideProps['direction']>('right');
  const slidesNumber = data.length;

  const classes = useStyle({
    image: `${data[currentSlide].image}`,
  });

  const [visible, setVisible] = useState<{
    in: boolean;
    classAnimation: string;
  }>({ in: true, classAnimation: classes.animated });

  const onExited = (): void => {
    setCurrentSlide(Math.abs(nextSlide % slidesNumber));
    setDirection(direction === 'right' ? 'left' : 'right');
    setVisible({ in: true, classAnimation: classes.animated });
  };

  const leftClick = (): void => {
    setNextSlide(nextSlide - 1);
    setDirection('right');
    setVisible({ in: false, classAnimation: '' });
  };

  const rightClick = (): void => {
    setNextSlide(nextSlide + 1);
    setDirection('left');
    setVisible({ in: false, classAnimation: '' });
  };

  return (
    <Grid className={classes.container}>
      <Slide direction={direction} in={visible.in} onExited={onExited}>
        <Box className={`${classes.slide} ${visible.classAnimation}`}>
          <Box className={classes.content}>
            <NavigateBeforeIcon onClick={leftClick} className={classes.arrow} />
            <Box>
              <p>{data[currentSlide].content}</p>
            </Box>
            <NavigateNextIcon onClick={rightClick} className={classes.arrow} />
          </Box>
        </Box>
      </Slide>
    </Grid>
  );
};
