import Tab from '@material-ui/core/Tab';
import Tabs from '@material-ui/core/Tabs';
import React, { FC, useEffect, useState } from 'react';
import { useHistory, useLocation } from 'react-router-dom';

export type TSideBarLink = {
  title: string;
  url: string;
};

type TSideBarProps = {
  sideBarLinks: TSideBarLink[];
};

export const SideBar: FC<TSideBarProps> = ({ sideBarLinks }) => {
  const [value, setValue] = useState<number | boolean>(0);
  const location = useLocation();
  const history = useHistory();

  useEffect(() => {
    // const path = location.pathname.slice(1);
    // let activeTab: number | boolean = subTitles.indexOf(path);
    // activeTab = activeTab >= 0 ? activeTab : false;
    // setValue(activeTab);
  }, []);

  if (sideBarLinks.length === 0 || sideBarLinks[0].title === '') return <></>;

  const tabs = sideBarLinks.map(({ title }, index) => (
    <Tab
      label={title}
      id={`vertical-tab-${index}`}
      aria-controls={`vertical-tabpanel-${index}`}
      key={`vertical-tab-${index}`}
    />
  ));
  const handleChange = (event: React.ChangeEvent<{}>, value: any) => {
    history.push(sideBarLinks[value].url);
  };

  const sideBar = (
    <Tabs
      orientation="vertical"
      variant="scrollable"
      value={value}
      onChange={handleChange}
      aria-label="Sidebar"
    >
      {tabs}
    </Tabs>
  );
  return <>{sideBar}</>;
};
