import React from 'react';
import { Switch, Route } from 'react-router-dom';

import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import { Header } from '../Header/Header';
import { Home } from '../../pages/Home/Home';
import { Footer } from '../Footer/Footer';
import { Gallery } from '../../pages/Gallery/Gallery';
import { Page } from '../../pages/Page/Page';
import { SubPage } from '../../pages/SubPage/SubPage';

const useStyles = makeStyles({
  page: {
    minHeight: '100vh',
  },
  content: {
    padding: 40,
    marginTop: 64,
    flex: 1,
  },
  footer: {
    backgroundColor: '#eee',
  },
});

export const Layout = () => {
  const classes = useStyles();

  return (
    <div>
      <Grid container direction="column" className={classes.page}>
        <Grid item container>
          <Header />
        </Grid>
        <Grid item className={classes.content} container justify="center">
          <Switch>
            <Route path={['', '/']} exact component={Home} />
            <Route path="/gallery" exact render={Gallery} />
            <Route path="/contact" exact render={() => <h1>Contact</h1>} />
            <Route path="/:category" exact component={Page} />
            <Route path="/:category/:subPage" exact component={SubPage} />
          </Switch>
        </Grid>
        <Grid item className={classes.footer} container>
          <Footer />
        </Grid>
      </Grid>
    </div>
  );
};
