import React, { useState, ChangeEvent, useEffect } from 'react';

import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Button from '@material-ui/core/Button';
import PhoneIcon from '@material-ui/icons/Phone';
import Box from '@material-ui/core/Box';
import { useHistory, useLocation } from 'react-router-dom';
import { Logo } from '../../Logo/Logo';

const useStyles = makeStyles({
  appBar: {
    backgroundColor: 'white',
    color: '#333',
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'space-around',
  },
  logo: {
    height: '60px',
    paddingTop: '2px',
    paddingBottom: '2px',
    cursor: 'pointer',
  },
  tab: {
    minWidth: 45,
    '&:hover': {
      color: '#f50057', //'#ad2503',
    },
  },
});

enum routePath {
  'windows' = 1,
  'doors',
  'materials',
  'service',
  'gallery',
  'about-us',
  'contact',
}

type TKeysLinks = keyof typeof routePath;

export const Header = () => {
  const location = useLocation<{ pathname: string }>();
  const history = useHistory();
  const classes = useStyles();

  const [active, setActive] = useState<number | boolean>(0);

  const handleChange = (event: ChangeEvent<{}>, newValue: number): void => {
    setActive(newValue);
  };

  const clickHandler = (path: string): void => {
    history.push('/' + path);
  };

  useEffect(() => {
    const path = location.pathname.slice(1).split('/')[0] as TKeysLinks;
    const activeLink = routePath[path];

    if ('' + path === '') {
      setActive(0);
    } else if (activeLink !== undefined) {
      setActive(activeLink);
    } else {
      setActive(false);
    }
  }, [location.pathname]);

  return (
    <AppBar className={classes.appBar}>
      <Box
        className={classes.logo}
        onClick={() => {
          history.push('/');
        }}
      >
        <Logo height />
      </Box>
      <Tabs value={active} onChange={handleChange}>
        <Tab
          label="Home"
          className={classes.tab}
          onClick={() => clickHandler('')}
        />
        <Tab
          label="Windows"
          className={classes.tab}
          onClick={() => clickHandler('windows')}
        />
        <Tab
          label="Doors"
          className={classes.tab}
          onClick={() => clickHandler('doors')}
        />
        <Tab
          label="Materials"
          className={classes.tab}
          onClick={() => clickHandler('materials')}
        />
        <Tab
          label="Service (Supply and fit)"
          className={classes.tab}
          onClick={() => clickHandler('service')}
        />
        <Tab
          label="Gallery"
          className={classes.tab}
          onClick={() => clickHandler('gallery')}
        />
        <Tab
          label="About us"
          className={classes.tab}
          onClick={() => clickHandler('about-us')}
        />
        <Tab
          label="Contact"
          className={classes.tab}
          onClick={() => clickHandler('contact')}
        />
      </Tabs>
      <Button startIcon={<PhoneIcon />} variant="outlined">
        +44 111 222 333
      </Button>
    </AppBar>
  );
};
